x;\n    width: 100%;\n    display: flex;\n    align-items: center;\n}\n\nq{\n    font-family: \"Bilbo\", cursive;\n    font-size: 1.5em;\n}\n\n.smaller-font{\n    font-size: 0.75em;\n}\n\n.flex-row img{\n    height: 100%;\n    min-width: 30%;\n    object-fit: cover;\n}\n\n.cuisine-panel{\n    padding: 0 200px;\n    background-color: var(--color-dark-blue);\n    color: var(--color-white);\n    & img{\n        width: 100%;\n        object-fit: contain;\n    }\n}\n\n.side-text{\n    padding: 50px;\n    color: currentColor;\n    font-size: 3.5rem;\n    font-weight: 700;\n    flex: auto;\n    text-align: center;\n}\n\n@keyframes floating{\n    0%{\n        transform: translateY(0px);\n    }\n    50%{\n        transform: translateY(-10px);\n    }\n    100%{\n        transition: translateY(0px);\n    }\n}\n\n.down-icon{\n    position: sticky;\n    width: 80px;\n    bottom: 50px;\n    animation: floating 1s ease-in-out infinite;\n}\n\n.down-icon:hover{\n    animation: none;\n}\n\n.down-icon:hover svg{\n    fill: var(--color-highlight);\n}\n\n#content .restaurant-img{\n    z-index: -1;\n    position: relative;\n    background-image: url('../../global_resources/shire.jpg');\n    background-repeat: no-repeat;\n    background-position-x: center;\n    background-position-y: -300px;\n    background-size: 1200px;\n    height: 750px;\n    width: 100%;\n}\n\n.wide-container{\n    position: relative;\n    width: 100%;\n    height: 650px;\n}\n\n.wide-container > img{\n    position: absolute;\n    object-fit: cover;\n    width: 100%;\n    height: 100%;\n    z-index: -1;\n}\n\n.captions{\n    position: absolute;\n    width: 100%;\n    color: var(--color-white);\n    font-size: 4rem;\n    font-weight: 700;\n    left: 50%;\n    top: 50%;\n    transform: translate(-50%, -50%);\n    text-shadow: 5px 5px 0 var(--color-highlight);\n    text-align: center;\n}\n\np{\n    max-inline-size: 66ch;\n    line-height: 2;\n}\n\n.food-menu, .drink-menu{\n    width: 50%;\n    margin: 0 auto;\n    padding: 25px;\n    display: grid;\n    place-content: center;\n    text-align: center;\n    gap: 50px;\n}\n\n.food-menu{\n    background-color: var(--color-background);\n    border-left: 5px solid var(--color-highlight);\n    border-right: 5px solid var(--color-highlight);\n    border-radius: 5px;\n}\n\n.drink-menu-background{\n    margin: 25px 0;\n    padding: 25px 0;\n    background-image: url(../../global_resources/eye_sauron.gif);\n    background-position: center;\n}\n\n.drink-menu{\n    background-color: var(--color-black);\n    border-left: 5px solid var(--color-white);\n    border-right: 5px solid var(--color-white);\n    border-radius: 5px;\n    color: var(--color-white);\n}\n\n.food-menu .title-card h4,.drink-menu .title-card h4{\n    font-size: 2rem;\n}\n\n.food-menu .title-card h5{\n    font-size: 0.8rem;\n    font-weight: 500;\n}\n\n.food-card{\n    padding: 10px 20px;\n    border-radius: 5px;\n}\n\n.drink-card{\n    padding: 20px 80px;\n    border-radius: 5px;\n}\n\n.food-card:hover{\n    background-color: var(--color-highlight);\n}\n\n.drink-card:hover{\n    background-color: var(--color-dark-blue);\n}\n\n.food-card .title, .drink-card .title{\n    font-weight: 700;\n}\n\n.food-card .description{\n    font-size: 1.6rem;\n    font-family: \"Bilbo\", cursive;\n}\n\n.food-card .availability{\n    font-size: 0.8rem;\n}\n\n.location-info{\n    width: 100%;\n    padding: 50px 250px;\n    display: flex;\n    justify-content: center;\n}\n\n.location-info .text-info{\n    /* background-color: red; */\n    width: 500px;\n    margin: 50px;\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    gap: 50px;\n}\n\n.location-info .text-info section{\n    display: grid;\n    grid-template-columns: 1fr 5fr;\n    align-items: center;\n    font-size: 1.4rem;\n    max-inline-size: 33ch;\n    font-weight: 700;\n    color: var(--color-dark-blue);\n    & svg{\n        fill: var(--color-footer);\n    }\n}\n\n.location-info .text-info table{\n    border-spacing: 60px 5px;\n    font-size: 1.4rem;\n    color: var(--color-footer);\n}\n\n.map-container{\n    border: 4px solid var(--color-highlight);\n    width: 800px;\n    min-width: 400px;\n}\n\n.reservation-container{\n    background-image: url(../../global_resources/gandalf_sitting.png);\n    background-repeat: no-repeat;\n    background-position: center;\n    background-size: cover;\n\n    height: 750px;\n    width: 100%;\n    margin-bottom: 45px;\n}\n\n.reservation-container section{\n    position: relative;\n    top: 20%;\n    left: 20%;\n    color: var(--color-white);\n    font-size: 3.5rem;\n    font-weight: 700;\n    text-shadow: 4px 4px 0 var(--color-footer);\n    width: max-content;\n}\n\n#reservation-button{\n    position: relative;\n    padding: 8px 50px;\n    top: 40%;\n    left: 20%;\n    font-size: 1.5rem;\n    font-weight: 700;\n    border: 4px solid var(--color-dark-blue);\n    background-color: var(--color-footer);\n    color: var(--color-black);\n    border-radius: 8px;\n    transition: 0.1s ease;\n}\n\n#reservation-button:hover{\n    color: var(--color-white);\n    background-color: var(--color-highlight);\n    border-color: var(--color-background);\n    box-shadow: 0 0 10px var(--color-white);\n}\n\n#reservation-dialog, #credits-dialog{\n    margin: auto;\n    max-width: 800px;\n    min-width: 400px;\n    width: auto;\n    padding: 30px;\n}\n\n#reservation-dialog{\n    background-color: var(--color-white);\n    border-color: var(--color-dark-blue);\n}\n\n#credits-dialog{\n    background-color: var(--color-black);\n    border-color: transparent;\n    color: var(--color-white);\n}\n\n#close-credits-button{\n    padding: 5px;\n    background-color: transparent;\n    border-radius: 50%;\n    border: none;\n    position: absolute;\n    right: 20px;\n    top: 20px;\n    & svg{\n        fill: var(--color-white);\n    }\n}\n\n#close-credits-button:hover{\n    background-color: var(--color-dark-blue);\n}\n\n#credits-dialog table{\n    margin: 0 auto;\n    width: 500px;\n    border-spacing: 0px 10px;\n}\n\n#credits-dialog table caption{\n    padding: 20px;\n}\n\n#credits-dialog img{\n    width: 200px;\n    height: 100px;\n    object-fit: cover;\n}\n\n#credits-dialog a{\n    color: var(--color-white);\n    font-size: 1.25rem;\n    text-align: right;\n    text-decoration: none;\n}\n\n#credits-dialog a:hover{\n    font-weight: 700;\n}\n\n#reservation-dialog::backdrop, #credits-dialog::backdrop{\n    background-color: var(--color-dark-tint);\n}\n\n#reservation-dialog h2{\n    text-align: center;\n}\n\n#close-form-button{\n    position: absolute;\n    right: 0;\n    top: 0;\n    margin: 10px;\n    border: none;\n    background: transparent;\n}\n\n#close-form-button:hover svg{\n    fill: var(--color-footer);\n}\n\n#reservation-form{\n    display: grid;\n    grid-template-columns: 1fr 1fr 1fr 1fr;\n    grid-template-rows: 1fr 1fr 1fr 2fr 1fr;\n    gap: 10px;\n    align-items: center;\n}\n\n#reservation-form label{\n    padding: 5px 10px;\n    font-weight: 700;\n    color: var(--color-black);\n    display: flex;\n    align-items: center;\n    gap: 5px;\n    & svg{\n        width: 25px;\n    }\n}\n\n#reservation-form input, #reservation-form select, #reservation-form textarea{\n    padding: 5px 8px;\n    border: 2px solid var(--color-dark-tint);\n    color: var(--color-dark-blue);\n    flex: auto;\n}\n\n.form-long-div, .form-half-div, .form-quarter-div{\n    display: flex;\n    flex-direction: column;\n    align-items: stretch;\n    justify-content: center;\n}\n\n.form-long-div{\n    grid-column: span 4;\n}\n\n.form-half-div{\n    grid-column: span 2;\n}\n\n#reservation-form button{\n    padding: 10px 40px;\n    background-color: var(--color-highlight);\n    border: none;\n    color: var(--color-white);\n    transition: 0.1s;\n}\n\n#reservation-form button:hover{\n    box-shadow: 0 0 10px var(--color-highlight);\n}\n\n\nfooter{\n    background-color: var(--color-footer);\n    color: var(--color-white);\n    font-size: 1rem;\n    position: fixed;\n    bottom: 0;\n    width: 100%;\n    height: 45px;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    gap: 20px;\n    text-align: center;\n}\n\nfooter a{\n    color: currentColor;\n    text-decoration: none;\n}\n\nfooter a:hover{\n    color: var(--color-highlight);\n}\n\n#footer-left, #footer-right{\n    background-repeat: no-repeat;\n    background-size: contain;\n    width: 337px;\n    height: 450px;\n    position: absolute;\n    bottom: -25px;\n}\n\n#footer-left{\n    background-image: url(../../global_resources/argonath_left.png);\n    background-position: left;\n    left: 0;\n}\n\n#footer-right{\n    background-image: url(../../global_resources/argonath_right.png);\n    background-position: right;\n    right: 0;\n}\n\n#footer-middle{\n    flex: auto;\n    align-self: flex-end;\n}\n\n\n"],"sourceRoot":""}]);
